.news-card {
  overflow: hidden;
  border-radius: var(--brad-regular);
  background: #ffffff;
}

.news-card--wide {
  grid-column: span 2;
}

.news-card__top {
  border-radius: 5px 5px 0 0;
  width: 100%;
  height: 24rem;
  overflow: hidden;
  position: relative;

  @media screen and (max-width: $tablet) {
    height: 15rem;
  }
}

.news-card__pic {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 5px 5px 0 0;
  overflow: hidden;
}

.news-card__img {
  object-fit: cover;
  object-position: center;
  transition: 0.4s;
}

.news-card__content {
  position: absolute;
  bottom: 1.5rem;
  left: 1.5rem;
  font-weight: 500;
  font-size: 1.4rem;
  color: #ffffff;
}

.news-card__info {
  padding: 1.5rem 1.5rem 2.5rem 1.5rem;
}

.news-card__descr {
  font-weight: 500;
  margin-bottom: 2rem;
  min-height: 3.5rem;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  transition: 0.4s;

  @media screen and (max-width: 1200px) {
    margin-bottom: 1rem;
  }
}

.news-card__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media screen and (max-width: 1200px) {
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column-reverse;
  }
}

.news-card__bottom .promo-carousel__date {
  padding-bottom: .5rem;
}

.news-card:hover .news-card__descr,
.news-card:hover .news-card__bottom {
  opacity: 0.7;
  transition: 0.3s ease-in-out;
}

.news-card:hover .news-card__img {
  transform: scale(1.1);
  transition: 0.3s ease-in-out;
}