header {
  background: #fff;
  position: relative;
  z-index: 6;

  @media screen and (max-width: 1450px) {
    border-bottom: 1px solid #ECECEC;
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__menu {
  display: flex;
  align-items: center;

  @media screen and (max-width: 1450px) {
    padding: 1rem 0;
    width: 100%;
  }

}

.header__menu .btn-burger {
  margin-right: 2rem;
  display: none;
  margin-top: 8px;

  @media screen and (max-width: 1450px) {
    display: block;
  }
}

.header__search {
  width: 46rem;
  margin-right: 4rem;

  @media screen and (max-width: 1450px) {
    width: 100%;
  }
}

.header__nav {
  @media screen and (max-width: 1450px) {
    display: none;
  }
}

.header__nav-links {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-transform: uppercase;
  transition: var(--tr);
}

.header__nav-item {
  font-weight: 700;
  transition: 0.4s;
  margin-right: 4rem;
  padding: 2rem 0;

  @media screen and (max-width: 1600px) {
    margin-right: 2rem;
  }
}

.header__nav-link {
  display: block;
  font-size: 1.4rem;
  transition: 0.4s;

  @media screen and (max-width: 1600px) {
    font-size: 1.2rem;
  }
}

.header__nav-link:hover {
  opacity: .7;
}

.header__nav-link:focus {
  opacity: 1;
  color: var(--c-accent);
}

.header__buttons {
  display: flex;
  align-items: center;

}

.header__geo {
  display: flex;
  align-items: center;
  margin-right: 2rem;

  @media screen and (max-width: 1450px) {
    margin-right: 0;
  }
}

.header__geo-icon {
  width: 3.2rem;
  height: 3.2rem;
  object-fit: cover;
  border: 1px solid #B4B4BF;
  border-radius: 50%;
  margin-right: 1rem;
  display: block;
}

.header__geo-text {
  font-weight: 700;
  text-transform: uppercase;
  transition: 0.4s;
}

.header__geo:hover .header__geo-text {
  opacity: 0.7;
}

.header-button {
  font-weight: 700;
  text-transform: uppercase;

  @media screen and (max-width: 1450px) {
    display: none;
  }
}


.header__nav-item-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 2;
  width: 100%;
  min-height: 3.13rem;
  background: #fff;
  border-top: 1px solid #ECECEC;
  opacity: 0;
  transition: 0.4s;
  pointer-events: none;
  cursor: default;
}

.header__nav-item-dropdown-content {
  height: 100%;
  padding: 1.5rem 0;
  display: flex;
  align-items: center;
}

.header__nav-item-dropdown:hover,
.header__nav-item:hover .header__nav-item-dropdown {
  opacity: 1;
  transition: 0.4s;
  pointer-events: all;
}