.has-youtube .fancybox__content,
.has-vimeo .fancybox__content,
.has-html5video .fancybox__content {
  width: 960px;
  height: 540px;
  max-width: 100%;
  max-height: 100%;
}

.has-map .fancybox__content,
.has-pdf .fancybox__content,
.has-youtube .fancybox__content,
.has-vimeo .fancybox__content,
.has-html5video .fancybox__content {
  padding: 0;
  background: rgba(24, 24, 27, 0.9);
  color: #fff;
}

.has-map .fancybox__content {
  background: #e5e3df;
}

.fancybox__html5video,
.fancybox__iframe {
  border: 0;
  display: block;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0);
}

.fancybox-placeholder {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

.f-carousel__thumbs {
  --f-thumb-width: 96px;
  --f-thumb-height: 72px;
  --f-thumb-outline: 0;
  --f-thumb-outline-color: #5eb0ef;
  --f-thumb-opacity: 1;
  --f-thumb-hover-opacity: 1;
  --f-thumb-selected-opacity: 1;
  --f-thumb-border-radius: 2px;
  --f-thumb-offset: 0px;
  --f-button-next-pos: 0;
  --f-button-prev-pos: 0;

  &.is-classic {
    --f-thumb-gap: 8px;
    --f-thumb-opacity: 0.5;
    --f-thumb-hover-opacity: 1;
    --f-thumb-selected-opacity: 1;
  }

  &.is-modern {
    --f-thumb-gap: 4px;
    --f-thumb-extra-gap: 20px;
    --f-thumb-clip-width: 46px;
  }
}

.f-thumbs {
  position: relative;
  flex: 0 0 auto;
  margin: 0;
  overflow: hidden;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
  perspective: 1000px;
  transform: translateZ(0);

  .f-spinner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    background-image: linear-gradient(#ebeff2, #e2e8f0);
    z-index: -1;

    svg {
      display: none;
    }
  }

  &.is-vertical {
    height: 100%;
  }
}

.f-thumbs__viewport {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.f-thumbs__track {
  display: flex;
  will-change: transform;
}

.f-thumbs__slide {
  position: relative;
  flex: 0 0 auto;
  box-sizing: content-box;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  width: var(--f-thumb-width);
  min-width: var(--f-thumb-width);
  height: var(--f-thumb-height);
  overflow: visible;
  cursor: pointer;

  &.is-loading img {
    opacity: 0;
  }
}

.is-classic .f-thumbs__viewport {
  height: 100%;
}

.is-modern {
  .f-thumbs__track {
    width: max-content;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: calc(var(--left, 0) * 1px);
      width: calc(100% - var(--width, 0) * 1px);
      cursor: pointer;
    }
  }

  .f-thumbs__slide {
    --clip-path: inset(
      0
        calc(
          (var(--f-thumb-width, 0) - var(--f-thumb-clip-width, 0)) * 0.5 *
            (1 - var(--progress, 0))
        )
        round var(--f-thumb-border-radius, 0)
    );
    transform: translate3d(calc(var(--shift, 0) * -1px), 0, 0);
    transition: none;
    pointer-events: none;

    &:focus-within:not(.is-selected) {
      filter: drop-shadow((-1px) 0px 0px var(--f-thumb-outline-color))
        drop-shadow(2px 0px 0px var(--f-thumb-outline-color))
        drop-shadow(0px -1px 0px var(--f-thumb-outline-color))
        drop-shadow(0px 2px 0px var(--f-thumb-outline-color));
    }

    > * {
      clip-path: var(--clip-path);
    }
  }

  &.in-touch .f-thumbs__slide {
    filter: none;
  }

  &.is-resting .f-thumbs__slide {
    transition: all 0.33s ease;

    > * {
      transition: all 0.33s ease;
    }
  }
}

.f-thumbs__slide__button {
  appearance: none;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  position: relative;
  border-radius: var(--f-thumb-border-radius);
  overflow: hidden;
  background: rgba(0, 0, 0, 0);
  outline: none;
  cursor: pointer;
  pointer-events: auto;
  touch-action: manipulation;
  opacity: var(--f-thumb-opacity);
  transition: opacity 0.2s ease;

  &:hover {
    opacity: var(--f-thumb-hover-opacity);
  }

  &:focus:not(:focus-visible) {
    outline: none;
  }

  &:focus-visible {
    outline: none;
    opacity: var(--f-thumb-selected-opacity);
  }
}

.is-nav-selected .f-thumbs__slide__button {
  opacity: var(--f-thumb-selected-opacity);

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: auto;
    bottom: 0;
    border: var(--f-thumb-outline, 0) solid
      var(--f-thumb-outline-color, transparent);
    border-radius: var(--f-thumb-border-radius);
    animation: f-fadeIn 0.2s ease-out;
    z-index: 10;
  }
}

.f-thumbs__slide__img {
  position: absolute;
  overflow: hidden;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: var(--f-thumb-offset);
  box-sizing: border-box;
  pointer-events: none;
  object-fit: cover;
}

.f-thumbs {
  &.is-horizontal {
    .f-thumbs__track {
      margin: 0 auto;
      padding: 8px 0 12px 0;
    }

    .f-thumbs__slide {
      margin: 0 var(--f-thumb-gap) 0 0;
    }
  }

  &.is-vertical {
    .f-thumbs__track {
      flex-wrap: wrap;
      margin: auto 0;
      padding: 0 8px;
    }

    .f-thumbs__slide {
      margin: 0 0 var(--f-thumb-gap) 0;
    }
  }
}

.fancybox__thumbs {
  --f-thumb-width: 96px;
  --f-thumb-height: 72px;
  --f-thumb-border-radius: 2px;
  --f-thumb-outline: 2px;
  --f-thumb-outline-color: #ededed;
  position: relative;
  opacity: var(--fancybox-opacity, 1);
  transition: max-height 0.35s cubic-bezier(0.23, 1, 0.32, 1);

  &.is-classic {
    --f-thumb-gap: 8px;
    --f-thumb-opacity: 0.5;
    --f-thumb-hover-opacity: 1;

    .f-spinner {
      background-image: linear-gradient(
        rgba(255, 255, 255, 0.1),
        rgba(255, 255, 255, 0.05)
      );
    }
  }

  &.is-modern {
    --f-thumb-gap: 4px;
    --f-thumb-extra-gap: 20px;
    --f-thumb-clip-width: 46px;
    --f-thumb-opacity: 1;
    --f-thumb-hover-opacity: 1;

    .f-spinner {
      background-image: linear-gradient(
        rgba(255, 255, 255, 0.1),
        rgba(255, 255, 255, 0.05)
      );
    }
  }

  &.is-horizontal {
    padding: 0 var(--f-thumb-gap);
  }

  &.is-vertical {
    padding: var(--f-thumb-gap) 0;
  }
}

.is-compact .fancybox__thumbs {
  --f-thumb-width: 64px;
  --f-thumb-clip-width: 32px;
  --f-thumb-height: 48px;
  --f-thumb-extra-gap: 10px;
}

.fancybox__thumbs.is-hidden {
  max-height: 0px !important;
}

.is-closing .fancybox__thumbs {
  transition: none !important;
}

.fancybox__toolbar {
  --f-progress-color: var(--fancybox-color, rgba(255, 255, 255, 0.94));
  --f-button-width: 46px;
  --f-button-height: 46px;
  --f-button-color: var(--fancybox-color);
  --f-button-hover-color: var(--fancybox-hover-color);
  --f-button-bg: rgba(24, 24, 27, 0.65);
  --f-button-hover-bg: rgba(70, 70, 73, 0.65);
  --f-button-active-bg: rgba(90, 90, 93, 0.65);
  --f-button-border-radius: 0;
  --f-button-svg-width: 24px;
  --f-button-svg-height: 24px;
  --f-button-svg-stroke-width: 1.5;
  --f-button-svg-filter: drop-shadow(1px 1px 1px rgba(24, 24, 27, 0.15));
  --f-button-svg-fill: none;
  --f-button-svg-disabled-opacity: 0.65;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI Adjusted",
    "Segoe UI", "Liberation Sans", sans-serif;
  color: var(--fancybox-color, currentColor);
  opacity: var(--fancybox-opacity, 1);
  text-shadow: var(
    --fancybox-toolbar-text-shadow,
    1px 1px 1px rgba(0, 0, 0, 0.5)
  );
  pointer-events: none;
  z-index: 20;

  :focus-visible {
    z-index: 1;
  }

  &.is-absolute {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
}

.is-compact .fancybox__toolbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.is-idle .fancybox__toolbar {
  pointer-events: none;
  animation: 0.15s ease-out both f-fadeOut;
}

.fancybox__toolbar__column {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: flex-start;

  &.is-left {
    flex-grow: 1;
    flex-basis: 0;
  }

  &.is-right {
    flex-grow: 1;
    flex-basis: 0;
    display: flex;
    justify-content: flex-end;
    flex-wrap: nowrap;
  }
}

.fancybox__infobar {
  padding: 0 5px;
  line-height: var(--f-button-height);
  text-align: center;
  font-size: 17px;
  font-variant-numeric: tabular-nums;
  -webkit-font-smoothing: subpixel-antialiased;
  cursor: default;
  user-select: none;

  span {
    padding: 0 5px;
  }

  &:not(:first-child):not(:last-child) {
    background: var(--f-button-bg);
  }
}

[data-fancybox-toggle-slideshow] {
  position: relative;

  .f-progress {
    height: 100%;
    opacity: 0.3;
  }

  svg g {
    &:first-child {
      display: flex;
    }

    &:last-child {
      display: none;
    }
  }
}

.has-slideshow [data-fancybox-toggle-slideshow] svg g {
  &:first-child {
    display: none;
  }

  &:last-child {
    display: flex;
  }
}

[data-fancybox-toggle-fullscreen] svg g {
  &:first-child {
    display: flex;
  }

  &:last-child {
    display: none;
  }
}

:fullscreen [data-fancybox-toggle-fullscreen] svg g {
  &:first-child {
    display: none;
  }

  &:last-child {
    display: flex;
  }
}

.f-progress {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  transform: scaleX(0);
  transform-origin: 0;
  transition-property: transform;
  transition-timing-function: linear;
  background: var(--f-progress-color, var(--f-carousel-theme-color, #0091ff));
  z-index: 30;
  user-select: none;
  pointer-events: none;
}
